<!-- 模块操作管理 -->
<template>
	<div class="container" >
		<h5 class="current title-color">{{$t('menuManage.curMenuTitle')}}</h5>
		<div class="tree-box">
			<el-aside width="100%" style="flex:1;">
				<el-tree class="float-children border-blue" :expand-on-click-node="false" empty-text @check="changeChecked" :data="menuTree" show-checkbox node-key="code" @node-click="handleNodeClick" empty-text highlight-current default-expand-all ref="tree" :props="defaultProps">
					<span :class="data.child && data.child.length == 0 ? 'float-children' : '' " class="custom-tree-node" slot-scope="{ node, data }">
						<span v-if="!data.child || data.child.length == 0" style="color:#00a8d4;font-size:14px;" class="icon iconfont">&#xe606;</span>
						<span v-if="data.child && data.child.length != 0" style="color:#00a8d4;font-size:12px;" class="icon iconfont">&#xe602;</span>
						<span v-if="data.type=='node' && !data.opend" style="color:#00a8d4;font-size:14px;" class="icon iconfont">&#xe642;</span>
						<span>{{ node.label }}</span>
					</span>
				</el-tree>
			</el-aside>
			<div class="submit-bar row-center">
				<el-button class="button" @click="addOperation" type="primary" size="small">{{ $t('button.submit') }}</el-button>
				<el-button class="button" type="primary" size="small">{{ $t('button.prve') }}</el-button>
			</div>
		</div>
	</div>
</template>

<script>
import store from '@/store'
var ParentFn = ()=>import('../../../../conmon/js/commo.js');
var Parent; 
export default {
	name: 'modelManage',
	data() {
		return {
			menuTree: [{
				id:'1',
				text: '功能菜单',
				type: 'node',
				opend: true,
				children: [{
					id:'2',
					type: 'node',
					text: '监控首页',
					opend: true,
					children: [{
						id:'7',
						type: 'children',
						text: '查询',
						onLine: false
					},
					{
						id:'9',
						type: 'children',
						text: '修改',
						onLine: true
					},
					{
						id:'10',
						type: 'children',
						text: '增加',
						onLine: true
					},
					{
						id:'11',
						type: 'children',
						text: '删除',
						onLine: true
					}]
				}]
			}],
			defaultProps: {
				children: 'child',
				label: 'name'
			}
		}
	},
	created() {
		// this.getTree();
	},
	//keep-alive激活时调用setCheckedKeys()
	activated(){
		this.getTree();
	},
	mounted() {

	},
	methods: {
		getTree(){
			var that = this;
			ParentFn().then(value=>{
				Parent = new value.default.ParentFn();
				//把中英文的参数赋值给Parent
				Parent.error = this.$t('error');
				Parent.success = this.$t('success');
				Parent.button = this.$t('button');
				Parent.message = this.$t('message');
				Parent.ajax(
					'api/services/app/SmModuleOperation/QueryAll',
					'',
					that,
					(res)=>{
						if(res.success && res.result.items.length > 0) {
							var list = res.result.items;
							that.menuTree = list;
							that.$nextTick(()=>{
								that.$refs.tree.setCurrentKey(list[0].id);
								var idList = that.getOperationId(list,[]);
								for(var i in idList) {
									that.$refs.tree.setChecked(idList[i],true);
								}
							})
						}
					}
					)
			})
			
		},
		//添加操作给模块
		addOperation(){
			var that = this;
			var curNode = this.$refs.tree.getCheckedNodes(false,true);
			var halfNode = this.$refs.tree.getHalfCheckedNodes();
			var modelIdList = {};
			modelIdList.inputs = [];
			var arr = this.getModelOperationId(curNode,curNode);
			var arr2 = this.getModelOperationId(halfNode,curNode);
			console.log(arr,arr2);
			for(var i in arr2) {
				arr.push(arr2[i]);
			}
			modelIdList.inputs = arr;
			Parent.ajax(
				'api/services/app/SmModuleOperation/CreateMore',
				modelIdList,
				that,
				(res)=>{
					if(res.success) {
						that.$message({
							type: 'success',
							message: Parent.success.add
						})
					}
				}
				)
		},
		//获取模块和id和操作id组成的数组
		getModelOperationId(arr,selectArr){
			var arr2 = [];
			for(var i in arr) {
				if(arr[i].type == '2' && arr[i].child && arr[i].child.length > 0) {
					arr2.push({moduleId: arr[i].id,operationId: this.getArrayId(arr[i].child,selectArr)})
				}
			};
			return arr2;
		},
		//遍历将操作id拼接成一个数组
		getArrayId(arr,selectArr) {
			var arr2 = [];
			for(var i in arr) {
				for(var j in selectArr) {
					if(arr[i].type == '3' && arr[i].code == selectArr[j].code) {
						arr2.push(arr[i].id);
					}
				}
			}
			return arr2;
		},
		//遍历获取已添加操作的id
		getOperationId(list,idList){
			for(var i in list) {
				if(list[i].type == '3' && list[i].state) {
					idList.push(list[i].code);
				}
				if(list[i].child && list[i].child.length > 0) {
					this.getOperationId(list[i].child,idList);
				}
			}
			return idList;	
		},
		changeChecked(){

		},
		handleNodeClick(){

		}
	},
	store,
	watch:{

	}
}
</script>

<style scoped>
.container{flex: 1;padding: 5px;background:#fff;display:flex;border: none;flex-direction: column;}
.current{border: 1px solid #ddd;border-bottom: none;padding: 0 10px;line-height: 40px;}
.tree-box{display: flex;flex-direction: column;flex: 1;border: 1px solid #ddd;}
.submit-bar{padding: 10px;overflow: hidden;}
.submit-bar .btn{float: right;margin-left: 10px;}
/*.float-children .el-tree-node__children .el-tree-node__children .el-tree-node{float: left !important;}*/
</style>


